.vote-detail-loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 64px);
}

.vote-detail {
  color: #ffffff;

  .content-container {
    background: #31333a;
    border-radius: 10px;
  }
  .content-container-shadow {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  }
  .back-to-vote-button {
    position: relative;
    display: block;

    color: #ffffff;
    opacity: 0.6;
    transition: none;

    &::after {
      content: '';
      background: url(../../images/v2/vote-detail/arrow-left-white.svg) 0 0 no-repeat;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &:hover {
      color: #9195fb;
      opacity: 1;

      &::after {
        background-image: url(../../images/v2/vote-detail/arrow-left-purple.svg);
      }
    }
  }

  .vote-detail-header {
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;

    .vote-detail-title-container {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .vote-detail-title {
        .proposal-id-tag {
          margin-left: 10px;
          padding: 4px 10px;

          font-weight: 500;
          font-size: 12px;
          line-height: 16px;

          color: rgba(255, 255, 255, 0.6);

          vertical-align: middle;
          background: rgba(255, 255, 255, 0.04);
          border-radius: 2px;
        }
      }
      .vote-detail-links-row {
        display: flex;
        flex-wrap: wrap;

        .link-item {
          position: relative;
          padding-right: 18px;

          color: #ffffff;
          opacity: 0.6;
          transition: all 0.3s;

          &::before {
            content: '';
            background: rgba(255, 255, 255, 0.16);
            border-radius: 2px;
            width: 12px;
            height: 12px;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
          }
          &::after {
            content: '';
            background: url(../../images/v2/vote-detail/new-tab-icon-white.svg) 0 0 no-repeat;
            width: 7px;
            height: 8px;
            position: absolute;
            top: 50%;
            right: 2px;
            transform: translateY(-50%);
          }

          &:hover {
            opacity: 1;
          }
        }
      }
    }

    $status-box-hover-animation: all 0.3s ease-in-out;
    .vote-detail-status-box {
      position: relative;
      .status-history {
        position: relative;
        height: 16px;
        display: flex;
        margin-bottom: 18px;
        transition: $status-box-hover-animation;

        .expand-button {
          transform: scale(0.8);
          top: 19px;
          left: 16px;
          position: absolute;
          // left: 22px;
          // top: 100%;
          // transform: translateY(4px);
          padding: 3px 6px;
          height: 16px;

          background: rgba(255, 255, 255, 0.1);
          border-radius: 2px;

          font-weight: 400;
          font-size: 8px;
          line-height: 11px;
          color: rgba(255, 255, 255, 0.6);

          cursor: grab;

          transition: $status-box-hover-animation;
          opacity: 0;

          &.visible {
            opacity: 1;
          }
        }
        .timeline-icon {
          position: relative;
          width: 16px;
          height: 16px;
          margin-right: 6px;

          &::after {
            content: '';
            position: absolute;
            width: 16px;
            height: 16px;
            top: 0;
            background: url(../../images/v2/vote-detail/status-succeeded.svg) 0 0 no-repeat;
          }
          &.succeeded::after {
            background: url(../../images/v2/vote-detail/status-succeeded.svg) 0 0 no-repeat;
          }
          &.failed::after {
            background: url(../../images/v2/vote-detail/status-failed.svg) 0 0 no-repeat;
          }
          &.pending::after {
            background: url(../../images/v2/vote-detail/status-pending.svg) 0 0 no-repeat;
          }
          &.waiting::after {
            background: url(../../images/v2/vote-detail/status-waiting.svg) 0 0 no-repeat;
          }
        }
        .timeline-dot {
          opacity: 0;
          position: absolute;
          width: 2px;
          height: 10px;
          top: 100%;
          left: 8px;
          transform: translate(-50%, 3px);
          background: url(../../images/v2/vote-detail/timeline-dot.png) 0 0 no-repeat;
          background-size: 2px 10px;
          transition: $status-box-hover-animation;
        }
        .timeline-arrow-dot {
          opacity: 0;
          position: absolute;
          width: 3px;
          height: 18px;
          top: 100%;
          left: 8px;
          transform: translate(-50%, 2px);
          background: url(../../images/v2/vote-detail/timeline-arrow-dot.png) 0 0 no-repeat;
          background-size: 3px 18px;
          transition: $status-box-hover-animation;
        }

        .status-title {
          flex-grow: 1;

          font-weight: 400;
          font-size: 14px;
          line-height: 16px;
          transition: all 0.3s ease-in-out;

          &.hover-enabled {
            &:hover {
              color: #ff5266;

              .hover-hint {
                opacity: 1;
              }
            }
          }

          .hover-hint {
            pointer-events: none;
            opacity: 0;

            position: absolute;
            padding: 12px 16px 12px 32px;
            left: -20px;
            top: -12px;
            transform: translateY(-100%);

            border-radius: 2px;
            background: #40414a;
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
            font-weight: 400;
            font-size: 12px;
            line-height: 20px;

            color: #ff5266;
            transition: all 0.2s ease-in-out;

            &::before {
              content: '';
              position: absolute;
              width: 12px;
              height: 10px;
              top: 16px;
              left: 16px;
              background: url(../../images/v2/vote-detail/alert.svg) 0 0 no-repeat;
              background-size: 12px 10px;
            }

            &::after {
              content: '';
              position: absolute;
              width: 0;
              height: 0;
              left: 50%;
              top: 100%;
              transform: translate(-50%, -1px);
              border-left: 10px solid transparent;
              border-right: 10px solid transparent;

              border-top: 6px solid #40414a;
            }
          }
        }
        .status-time {
          position: relative;
          margin-left: 6px;

          font-weight: 400;
          font-size: 12px;

          color: rgba(255, 255, 255, 0.6);
        }

        &:not(:last-child):not(.expandable) {
          .timeline-dot {
            opacity: 1;
          }
        }
        &.expandable {
          margin-bottom: 24px;

          .timeline-arrow-dot {
            opacity: 1;
          }
        }
        &.collapsed {
          height: 0;
          margin: 0;
          opacity: 0;
        }
      }

      &.invisible-content {
        .status-history {
          opacity: 0;
        }
      }

      &.expandable-display {
        position: absolute;
        z-index: 999;
        top: 0;
        left: 0;

        margin-left: 0;
        pointer-events: none;

        transition: $status-box-hover-animation;

        .status-history:not(.collapsed) {
          opacity: 1;
        }
        &.expanding {
          background: #40414a;
          box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
        }
      }
    }
  }

  $progress-bar-border-radius: 10px;
  .progress-bar-container {
    display: flex;
    flex-direction: row;

    margin-bottom: 20px;

    &.no-vote {
      .support-progress-bar-container {
        .progress-bar {
          background: #4d6e6c;
        }
      }
      .against-progress-bar-container {
        .progress-bar {
          background: #6f3c47;
        }
      }
    }

    .support-progress-bar-container,
    .against-progress-bar-container {
      flex-grow: 1;
      padding-bottom: 14px;

      position: relative;
      display: flex;

      background-color: #31333a;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: $progress-bar-border-radius;

      .progress-bar-title,
      .progress-bar-vote-string,
      .wallet-vote-container,
      .action-button {
        position: relative;
        z-index: 1;
      }
      .progress-bar-vote-string {
        flex-grow: 1;
      }
      .wallet-vote-container {
        position: relative;

        .vote-icon {
          position: absolute;
          background: url(../../images/v2/vote-detail/vote.svg) 0 0 no-repeat;
          background-size: 18px 20px;
          width: 18px;
          height: 20px;
          left: 0;

          &:hover {
            background-image: url(../../images/v2/vote-detail/vote-hover.svg);

            .voted-hint {
              opacity: 1;
            }
          }

          .voted-hint {
            pointer-events: none;
            opacity: 0;

            position: absolute;
            padding: 12px 16px;
            left: 10px;
            top: -15px;
            width: 140px;
            transform: translate(-50%, -100%);

            border-radius: 2px;
            background: #40414a;
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);

            font-weight: 400;
            font-size: 12px;
            text-align: center;

            transition: all 0.2s ease-in-out;

            &::after {
              content: '';
              position: absolute;
              width: 0;
              height: 0;
              left: 50%;
              top: 100%;
              transform: translate(-50%, -1px);
              border-left: 10px solid transparent;
              border-right: 10px solid transparent;

              border-top: 6px solid #40414a;
            }
          }
        }
      }
      .action-button {
        padding: 6px 12px;
        color: #ffffff;
        min-width: 90px;
        border-radius: 2px;

        text-transform: capitalize;
        text-align: center;

        &:disabled {
          color: rgba(255, 255, 255, 0.6);
          background: #53545a;
          cursor: not-allowed;
          &:hover {
            color: rgba(255, 255, 255, 0.6);
            background: #53545a;
          }
        }
        transition: all 0.3s ease-in-out;
      }

      .progress-bar {
        position: absolute;
        bottom: 0;

        width: 100%;

        border-radius: $progress-bar-border-radius;

        &.square-upper-half {
          border-radius: 0;
        }
      }

      .progress-bar-background {
        position: absolute;
        z-index: 0;
        top: 0;
        width: 100px;
        height: 100%;

        background-color: #31333a;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
        border: none;
        border-radius: $progress-bar-border-radius;
        overflow: hidden;

        .progress-bar {
          border-radius: 0;
        }
      }

      &.zero-vote {
        .progress-bar {
          background: #41434a;
        }
      }
    }

    .support-progress-bar-container {
      flex-basis: 100%;

      .wallet-vote-container {
        .wallet-vote-count {
          color: #18c19f;
        }
      }
      .action-button {
        background: #18c19f;
        &:hover {
          background: #74dac5;
        }
      }

      .progress-bar {
        background: #8ff7e2;
      }
      .progress-bar-background {
        right: 0;
      }
    }
    .against-progress-bar-container {
      flex-basis: 100%;

      .wallet-vote-container {
        .wallet-vote-count {
          color: #ff5266;
        }
      }
      .action-button {
        background: #ff5266;
        &:hover {
          background: #ffaab4;
        }
      }

      .progress-bar {
        background: #ff5266;
      }
      .progress-bar-background {
        left: 0;
        top: 0;
      }
    }
  }

  .connect-wallet-hint-bar,
  .redeem-vote-bar {
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    .bar-title {
      flex-grow: 1;
    }
    .bar-action-btn {
      padding: 2px 20px 0px;
      color: #fff;
      background: #4d54ff;
      border-radius: 2px;
      transition: all 0.3s ease-in-out;
      &:hover {
        background: #9195fb;
      }

      &:disabled {
        color: rgba(255, 255, 255, 0.6);
        background: #53545a;
        cursor: not-allowed;
      }
    }
  }

  .vote-detail-info-container {
    margin-top: 20px;

    .vote-detail-info-header {
      border-bottom: 1px solid rgba(255, 255, 255, 0.06);
    }
  }
}

