            }
            &.waiting::after {
              background-image: url(../../images/v2/vote-detail/status-waiting-white-theme.svg);
            }
          }
          .timeline-dot {
            background-image: url(../../images/v2/vote-detail/timeline-dot-white-theme.png);
          }
          .timeline-arrow-dot {
            background-image: url(../../images/v2/vote-detail/timeline-arrow-dot-white-theme.png);
          }
          .status-title {
            .hover-hint {
              &::before {
                background-image: url(../../images/v2/vote-detail/alert-white-theme.svg);
              }
            }
          }
          .status-time {
            color: rgba(34, 35, 43, 0.6);
          }
        }

        &.expandable-display {
          &.expanding {
            background: #ffffff;
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
          }
        }
      }
    }

    .progress-bar-container {
      .support-progress-bar-container,
      .against-progress-bar-container {
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
        background-color: #ffffff;

        .progress-bar-background {
          box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
          background-color: #ffffff;
        }

        .wallet-vote-container {
          .vote-icon {
            background-image: url(../../images/v2/vote-detail/vote-white-theme.svg);

            &:hover {
              background-image: url(../../images/v2/vote-detail/vote-white-theme-hover.svg);
            }
          }

          .voted-hint {
            color: #ffffff;

            &::after {
              border-top: 6px solid #40414a;
            }
          }
        }

        &.zero-vote {
          .progress-bar {
            background: #f6f6f7;
          }
        }

        .action-button {
          &:disabled {
            color: #ffffff;
            background: #d3d3d5;
            cursor: not-allowed;
          }
        }
      }
      .support-progress-bar-container {
        .progress-bar {
          background: #18c19f;
        }
      }
      &.no-vote {
        .support-progress-bar-container {
          .progress-bar {
            background: #d1f3ec;
          }
        }
        .against-progress-bar-container {
          .progress-bar {
            background: #ffdce0;
          }
        }
      }
    }

    .connect-wallet-hint-bar,
    .redeem-vote-bar {
      .bar-action-btn {
        &:disabled {
          color: #ffffff;
          background: #d3d3d5;
          cursor: not-allowed;
        }
      }
    }

    .vote-detail-info-container {
      .vote-detail-info-header {
        border-bottom: 1px solid rgba(34, 35, 43, 0.1);
      }
    }
  }
}
